@import url("https://fonts.googleapis.com/css?family=Roboto+Slab:100,300,400,700");
@import url("https://fonts.googleapis.com/css?family=Raleway:300,300i,400,400i,500,500i,600,600i,700,700i,800,800i,900,900i");

@font-face{
  font-family: "OneDay";
  src:url(ONEDAY.ttf)
}



* {
  margin: 0;
  border: 0;
  box-sizing: border-box;
}

:root {
  --font-roboto: "Roboto Slab", serif;
  --font-raleway: "Raleway", sans-serif;
  --font-lato: "Lato";
  --color-button: #DDD0C8
}

body {
  font-family: "OneDay";
  background-color: #0B0C10;
}

.app {
  padding: 4rem;

  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

h1 {
  font-size: 3rem;
  letter-spacing: 0.9px;
  background-color: #04d9ff;
  letter-spacing: 0.4em;
  background-clip: text;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  width: fit-content;
}

.search {
  width: 71%;
  margin: 4rem 0 2rem;

  display: flex;
  align-items: center;
  justify-content: center;

  padding: 1.5rem 1.75rem;
  border-radius: 3rem;
  background: #1F2833;
  
}

.search input {
  flex: 1;
  border: none;
  font-size: 1.5rem;
  font-family: var(--font-raleway);
  font-weight: 500;
  padding-right: 1rem;
  outline: none;
  color: #a1a1a1;
  background: #1F2833;
}

.search img {
  width: 35px;
  height: 35px;
  margin-left: 10px;
  margin-right: 10px;
  cursor: pointer;
}

.filter {
  width: 71%;
  

  display: flex;
  align-items: center;
  justify-content: center;

  padding: 1.5rem 1.75rem;
  border-radius: 3rem;
  background: #1F2833;
  border-color: #04d9ff;
  flex-wrap: wrap;
}

.filter button{
  width: 6.25rem;
  height: 2.5rem;
  margin-left: 1.25rem;
  margin-right: 1.25rem;
  
  cursor: pointer;
  color: #C5C6C7;
  background: #1F2833;
  font-weight:600;
}






