.empty {
  width: 100%;
  margin-top: 3rem;

  display: flex;
  justify-content: center;
  align-items: center;
}

.empty h2 {
  font-size: 1.25rem;
  color: #04d9ff;
  font-family: var(--font-raleway);
  font-weight: 500;
}

.container {
  width: 100%;
  margin-top: 3rem;

  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
}

.recipe {
  width: 400px;
  height: 296px;
  margin: 1.5rem;

  position: relative;
  border-radius: 12px;
  overflow: hidden;
  border: none;

  transition: all 0.4s cubic-bezier(0.175, 0.885, 0, 1);
  box-shadow: 0px 13px 10px -7px rgba(0, 0, 0, 0.1);
}

.recipe div:nth-of-type(1) {
  position: absolute;
  padding: 16px;
  width: 100%;
  opacity: 0;
  top: 0;
  color: #04d9ff;
  font-family: var(--font-raleway);
}

.recipe:hover {
  box-shadow: 0px 30px 18px -8px rgba(0, 0, 0, 0.1);
  transform: scale(1.05, 1.05);
  cursor: pointer;
}

.recipe div:nth-of-type(2) {
  width: 100%;
  height: 100%;
}

.recipe div:nth-of-type(2) img {
  height: 100%;
  width: 100%;
}

.recipe div:nth-of-type(3) {
  z-index: 2;
  background-color: #1F2833;
  padding: 16px 24px 24px 24px;
  position: absolute;
  bottom: 0;
  right: 0;
  left: 0;
}



.recipe div:nth-of-type(3) h3 {
  margin-top: 5px;
  font-family: "Raleway", sans-serif;
  color: #C5C6C7;
}

.recipe:hover div:nth-of-type(2) {
  height: 100%;
  opacity: 0.3;
}

.recipe:hover div:nth-of-type(3) {
  background-color: transparent;
}

.recipe:hover div:nth-of-type(3) h3 {
  color: transparent;
}

.recipe:hover div:nth-of-type(1) {
  opacity: 1;
}


@media screen and (max-width: 1000px) {
  .filter button{
    margin-left: 0.5rem;
    margin-right: 0.5rem;
  }
}

@media screen and (max-width: 800px) {
  .search {
    padding: 1rem 1.75rem;
    width: 100%;
  }

  .filter {
    padding: 1rem 1.75rem;
    width: 100%;
  }

  .filter button{
    margin-left: 0.05rem;
    margin-right: 0.05rem;
  }
}



@media screen and (max-width: 600px) {
  .app {
    padding: 4rem 2rem;
  }

  .search input {
    font-size: 1rem;
  }

  .search img {
    width: 20px;
    height: 20px;
  }

  .filter button{
    width: 4rem;
    height: 3rem;
    margin-left: 0.2rem;
    margin-right: 0.2rem;
    font-weight:300;
    background: transparent;
  }
}



@media screen and (max-width: 400px) {
  .app {
    padding: 4rem 1rem;
  }

  h1 {
    font-size: 2rem;
  }

  .container {
    margin-top: 2rem;
  }

  .recipe {
    width: "100%";
    margin: 1rem;
  }
}
